<template>
<div>

  <table class="My_table">
    <tr>
      <th>姓名</th>
      <th>级别</th>
      <th>获奖时间</th>
      <th>获奖内容</th>
      <th>获奖等级</th>
      <th>操作</th>
    </tr>
    <tr v-for="(item, index) in data" :key="index">
      <td>{{item.name}}</td>
      <td>{{item.classify}}</td>
      <td>{{item.date}}</td>
      <td>{{item.detail}}</td>
      <td>{{item.level}}</td>
      <td><a href="#"><i class="fa fa-pencil-square-o" aria-hidden="true"></i></a><span>&nbsp;||&nbsp;</span>
        <a href="#"><i class="fa fa-trash" aria-hidden="true"></i></a></td>
    </tr>
  </table>
  <div id="main2" style="width: 500px;height:200px;"></div>
</div>
</template>

<script>
export default {
  name: "honor",
  data (){
    return {
      data: '',
    }
  },
  methods: {
    drawChart() {
      // const myChart;
      // if (myChart != null && myChart != undefined) {
      //   myChart.dispose()
      // }
      const myChart = this.$echarts.init(document.getElementById('main2'))
      const option = {
        title: {
          text: 'Echarts入门'
        },
        tooltip: {},
        legend: {
          data: ['销量']
        },
        xAxis: {
          data: ['2', '3', '9']
        },
        yAxis: {},
        series: [
          {
            name: '销量',
            type: 'bar',
            data: [2, 6, 7]
          }
        ]
      }
      myChart.setOption(option)
    },
    delChart (){
      document.getElementById('main2').style.display = 'none'
    }
  },
  created (){
    // this.drawChart()
  },
  mounted (){
    this.drawChart()
  },
  beforeRouteLeave(to,from,next){
    console.log(to)
    console.log(from)
    // this.drawChart
    // this.drawChart().$destroy()
    this.delChart()
    next()

  }
}
</script>

